.torrent-tips {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
}
.torrent-tips__step-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.torrent-tips__step-1 h2 {
  color: #fff;
}
.step-1_discription {
  color: #fff;
  font-size: 2rem;
  margin: 2rem 0;
  line-height: 2;
  text-align: center;
  padding: 1rem;
}
.popular_sites > ul {
  display: flex;
}
.popular_sites > ul li {
  background-color: dodgerblue;
  margin-right: 1rem;
  border-radius: 0.5rem;
  transition: background-color 0.4s;
}
.popular_sites > ul li:hover {
  background-color: coral;
}
.popular_sites__name {
  font-size: 2rem;
  display: block;
  text-align: center;
  padding: 2rem;
  color: #fff;
}
@media screen and (max-width: 1024px) {
  .popular_sites > ul {
    flex-direction: column;
  }
  .popular_sites > ul li {
    margin: 3rem 0 0 0;
  }
  .step-1_discription {
    font-size: 2rem;
  }
}
