.navbar {
  height: 7rem;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar__list {
  display: flex;
}
.navbar__list li {
  padding-left: 1rem;
}
.navbar__list li a {
  display: block;
  color: #000;
  padding: 2rem 1.5rem;
  font-size: 2rem;
  transition: all 0.3s;
}
.navbar__list li a:hover {
  background-color: orangered;
  color: #fff;
}
