.footer {
  background: #fff;
  display: flex;
  height: 20vh;
  justify-content: center;
  align-items: center;
}
.footer p {
  color: #000;
  font-size: 2.5rem;
}
.profile {
  border-radius: 50%;
  height: 4rem;
  margin-left: 1rem;
}
@media screen and (max-width:800px) {
  .footer{
    flex-direction: column;
  }
  .footer p{
    padding: 1rem;
    text-align: center;
    font-size: 1.7rem;
  }
}